import React from 'react';
import { Card as BsCard, CardBody, CardHeader } from 'reactstrap';
import cn from 'classnames';

import styles from './styles.module.scss';

const Card = ({ children, dark, className, bodyClass, header, ...rest }) => (
  <BsCard className={cn(styles.card, className, (() => dark && styles.dark)())} {...rest}>
    {header && <CardHeader className={styles.cardHeader}>{header}</CardHeader>}
    <CardBody className={bodyClass}>{children}</CardBody>
  </BsCard>
);

Card.defaultProps = {
  dark: false,
  header: undefined,
};

export default Card;
