import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Col, Row, Button } from 'reactstrap';

import Card from '../card';

import styles from './styles.module.scss';

const AddCamera = ({ text, onClick }) => {
  return (
    <Card className={styles.container}>
      <Row alignItems="space-between">
        <Col className={`${styles.body} `} align="center">
          <Button color="default" onClick={onClick}>
            <FontAwesomeIcon className={styles.icon} size="3x" icon="video" />
            <span className={`${styles.text}`}>{text}</span>
          </Button>
        </Col>
      </Row>
    </Card>
  );
};

AddCamera.defaultProps = {
  text: 'Add camera',
  onClick: () => {},
};

export default AddCamera;
